           <div class="clearA"></div>
           <el-button id="butt" style="margin: 20px auto;" type="primary" @click="QueryAttribute">我已阅读以下规则，现在发布宝贝</el-button>
           <div style="text-align: left;width: 685px;height: 150px;overflow: auto;margin: 0px auto;border: 1px solid rgb(198, 198, 198);padding: 0px 20px;">
             <h5>发布须知：</h5><p>淘宝禁止发布侵犯他人知识产权的商品，请确认商品符合知识产权保护的规定</p><p><br></p><h5>淘宝规则</h5><p><br></p><h5>第一章 概述</h5><h5>第一条</h5><p>为促进开放、透明、分享、责任的新商业文明，保障淘宝用户合法权益，维护淘宝正常经营秩序，根据《大淘宝宣言》及《淘宝服务协议》、《tmall.com(天猫)服务协议》，制定本规则。</p><h5>第二条</h5><p>淘宝规则，是对淘宝用户增加基本义务或限制基本权利的条款。</p><h5>第三条</h5><p>违规行为的认定与处理，应基于淘宝认定的事实并严格依规执行。淘宝用户在适用规则上一律平等。</p><h5>第四条</h5><p>用户应遵守国家法律、行政法规、部门规章等规范性文件。对任何涉嫌违反国家法律、行政法规、部门规章等规范性文件的行为，本规则已有规定的，适用本规则；本规则尚无规定的，淘宝有权酌情处理。但淘宝对用户的处理不免除其应尽的法律责任。用户在淘宝的任何行为，应同时遵守与淘宝及其关联公司所签订的各项协议。
 
淘宝有权随时变更本规则并在网站上予以公告。若用户不同意相关变更，应立即停止使用淘宝的相关服务或产品。淘宝有权对用户行为及应适用的规则进行单方认定，并据此处理。</p><h5>第二章 定义</h5><h5>第五条</h5><p>淘宝，包括淘宝网，域名为 taobao.com；tmall.com(天猫)，域名为 tmall.com；一淘网，域名为 etao.com。</p><h5>第六条</h5><p>用户，指具有完全民事行为能力的淘宝各项服务的使用者。</p><h5>第七条</h5><p>会员，指与淘宝签订《淘宝服务协议》和/或《tmall.com(天猫)服务协议》并完成注册流程的用户。一个会员可以拥有多个账户，每个账户对应唯一的会员名。</p><h5>第八条</h5><p>买家，指在淘宝上浏览或购买商品的用户。</p><h5>第九条</h5><p>卖家，指在淘宝上发布商品的会员，tmall.com(天猫)卖家也称为“商家”。</p><h5>第十条</h5><p>拍下，指买家在淘宝上点击并确认购买的行为。</p><h5>第十一条</h5><p>订单，指买家向单一卖家同一时间拍下单款或多款商品的合约。订单中针对任一款商品的内容构成独立的交易。</p><h5>第十二条</h5><p>绑定，指淘宝账户与支付宝账户一一对应。</p><h5>第十三条</h5><p>商品发布数量，指卖家在淘宝上出售中及在线上仓库中商品款数的总和。</p><h5>第十四条</h5><p>退货运费险,指保险公司为投保该险种的淘宝会员支付单次退货运费。</p><h5>第十五条</h5><p>分销商品，指淘宝分销平台分销商从其供应商获取并出售的商品。</p><h5>第十六条</h5><p>节点处理，指当会员违规扣分累计达到一定分值时而被执行处理的过程。</p><h5>第十七条</h5><p>店铺装修区，指店铺招牌、宝贝分类、公告栏、促销区、广告牌等店铺相关模块。</p><h5>第十八条</h5><p>成交，指买家在淘宝上拍下商品并成功付款到支付宝。货到付款交易中买家拍下商品即视为成交。</p><h5>第十九条</h5><p>下架，指将出售中的商品转移至线上仓库。</p><h5>第二十条</h5><p>包邮，指卖家对所售商品承担大陆地区首次发货的运费。</p><h5>第三章 交易</h5><h5>第一节 注册</h5><h5>第二十一条</h5><p>会员应当严格遵循淘宝系统设置的注册流程完成注册。会员在选择其淘宝会员名、淘宝店铺名或域名时应遵守国家法律法规，不得包含违法、涉嫌侵犯他人权利或干扰淘宝运营秩序等相关信息。会员名注册后无法自行修改。淘宝有权收回未通过支付宝实名认证且连续一年未登录淘宝或阿里旺旺的会员名。</p><h5>第二十二条</h5><p>会员符合以下任一情形的，其淘宝账户不得与支付宝账户取消绑定：（一）已通过支付宝实名认证且发布过商品或创建过店铺；（二）尚有未完结的交易或投诉举报；（三）支付宝账户尚未被激活或尚有被冻结款项。</p><h5>第二节 经营</h5><h5>第二十三条</h5><p>会员须符合以下条件，方可按照淘宝系统设置的流程创建店铺：（一）将其账户与通过实名认证的支付宝账户绑定；（二）公示或披露真实有效的姓名地址或营业执照等信息。</p><h5>第二十四条</h5><p>会员应当按照淘宝系统设置的流程和要求发布商品。</p><h5>第二十五条</h5><p>“商品如实描述”及对其所售商品质量承担保证责任是卖家的基本义务。“商品如实描述”是指卖家在商品描述页面、店铺页面、阿里旺旺等所有淘宝提供的渠道中，应当对商品的基本属性、成色、瑕疵等必须说明的信息进行真实、完整的描述。卖家应保证其出售的商品在合理期限内可以正常使用，包括商品不存在危及人身财产安全的不合理危险、具备商品应当具备的使用性能、符合商品或其包装上注明采用的标准等。</p><h5>第三节 超时规定</h5><h5>第二十六条</h5><p>会员应当按照淘宝系统设置的交易流程进行交易。</p><h5>第二十七条</h5><p>自买家拍下或卖家最后修改交易条件之时起三天内，买家未付款的，交易关闭。</p><h5>第二十八条</h5><p>买家自付款之时起即可申请退款。自买家申请退款之时起两天内卖家仍未点击发货的，淘宝通知支付宝退款给买家。</p><h5>第二十九条</h5><p>自卖家在淘宝确认发货之时起，买家未在以下时限内确认收货且未申请退款的，淘宝通知支付宝打款给卖家：(一)自动发货商品一天内；(二)虚拟商品三天内；(三)快递、EMS及不需要物流的商品十天内；(四)平邮商品三十天内。</p><h5>第三十条</h5><p>买家申请退款后，依以下情况分别处理：(一)卖家拒绝退款的，买家有权修改退款协议、要求淘宝介入或确认收货。买家在卖家拒绝退款后七天内未操作的，退款流程关闭，交易正常进行；(二)卖家同意退款或在五天内未操作的，且不要求买家退货的，淘宝通知支付宝退款给买家；(三)卖家同意退款或五天内未操作的，且要求买家退货的，则按以下情形处理：1、买家未在七天内点击退货的，退款流程关闭，交易正常进行；2、买家在七天内点击退货，且卖家确认收货的，淘宝退款给买家；3、买家在七天内点击退货，通过快递退货十天内、平邮退货三十天内，卖家未确认收货的，淘宝通知支付宝退款给买家。</p><h5>第四节 评价</h5><h5>第三十一条</h5><p>买卖双方有权基于真实的交易在支付宝交易成功后十五天内进行相互评价。评价包括“信用评价”和/或“店铺评分”，tmall.com(天猫)评价仅包括店铺评分。</p><h5>第三十二条</h5><p>在信用评价中，评价人若给予好评，则被评价人信用积分增加一分；若给予差评，则信用积分减少一分；若给予中评或十五天内双方均未评价，则信用积分不变。如评价人给予好评而对方未在十五天内给其评价，则评价人信用积分增加一分。评价人可在评价生效后的三个月内追加评论，评论的内容不得修改，被评价人可对追加的评论内容进行解释。追加评论不影响被评价人的信用积分。相同买、卖家任意十四天内就同款商品的多笔支付宝交易，多个好评只加一分、多个差评只减一分。每个自然月，相同买家与淘宝网卖家之间交易，双方增加的信用积分均不得超过六分；相同买家与tmall.com(天猫)卖家之间交易，买家信用积分仅计取前三次。评价人可在作出中、差评后的三十天内，对信用评价进行一次修改或删除。三十天后评价不得修改。淘宝有权删除评价内容中所包含的污言秽语。</p><h5>第三十三条</h5><p>店铺评分由买家对卖家作出，包括宝贝与描述相符、卖家服务态度、卖家发货速度、物流发货速度四项。每项店铺评分均为动态指标，系此前连续六个月内所有评分的算术平均值。买家若完成对tmall.com(天猫)卖家店铺评分中宝贝与描述相符一项的评分，则其信用积分增加一分。每个自然月，相同买、卖家之间交易，卖家店铺评分仅计取前三次。
 店铺评分一旦做出无法修改。</p><h5>第四章 市场管理</h5><h5>第一节 市场管理措施</h5><h5>第三十四条</h5><p>为了提升消费者的购物体验，维护淘宝市场正常运营秩序，淘宝按照本规则规定的情形对会员及其经营行为采取以下临时性市场管控措施：(一)警告，是指淘宝通过口头或书面的形式对会员的不当行为进行提醒和告诫；(二)商品下架，是指将会员出售中商品转移至线上仓库；(三)单个商品搜索降权，是指调整商品在搜索结果中的排序；(四)全店商品搜索降权，是指调整会员店铺内所有商品在搜索结果中的排序；(五)单个商品搜索屏蔽，是指商品在搜索结果中不展现；(六)单个商品单一维度搜索默认不展示，是指商品信息在按价格、信用、销量等单一维度的搜索结果中默认不展现，但可经消费者主动选择后展现；(七)全店商品单一维度搜索默认不展示，是指会员店铺内所有商品在按价格、信用、销量等单一维度的搜索结果中默认不展现，但可经消费者主动选择后展现；(八)限制参加营销活动，是指限制卖家参加淘宝官方发起的营销活动；(九)单个商品监管，是指在一定时间内商品信息无法通过搜索、商品链接等方式查看；(十)店铺监管，是指在一定时间内会员店铺及店铺内所有商品信息无法通过搜索、店铺或商品链接等方式查看；(十一)支付违约金，是指根据协议约定或本规则规定由卖家向买家和/或淘宝支付一定金额的违约金。(十二)卖家绑定的支付宝收款账户的强制措施，是指对与卖家的淘宝账户绑定的支付宝收款账户采取的限制措施，包括但不限于取消收款功能、取消提现功能、禁止余额支付、冻结资金、冻结交易、永久冻结支付宝账户等。</p><h5>第二节 市场管理情形</h5><h5>第三十五条</h5><p>卖家应积极提升自身经营状况，为消费者提供高品质的商品及优质的服务。对于符合商品品质好、服务质量高等情形的卖家，淘宝会适当给予鼓励或扶持的措施。</p><h5>第三十六条</h5><p>淘宝根据卖家的好评率、店铺评分、退款率及纠纷退款率等，对低于淘宝指定标准的卖家限制参加淘宝营销活动，且给予全店商品在淘宝网单一维度搜索默认不展示。特殊市场另有规定的从其特殊规定。</p><h5>第三十七条</h5><p>卖家所发布的商品信息各要素应真实合理，且应与商品本身相符。淘宝对符合下列任一情形的卖家给予全店商品在淘宝网单一维度搜索默认不展示：1、所发布商品一口价与描述价格严重不符的；2、刻意规避淘宝商品SKU设置规则的；3、商品标题、图片、价格、物流方式、邮费及售后服务等商品要素之间明显不匹配的；4、通过拆分数量、单位等规避搜索单一维度排序规则的。</p><h5>第五章 通用违规行为及违规处理</h5><h5>第一节 违规处理措施</h5><h5>第三十八条</h5><p>为保障消费者、经营者或淘宝的正当权益，在会员违规处理期间淘宝按照本规则规定的情形对会员采取以下违规处理措施：(一)店铺屏蔽，指在搜索、导航、营销等各项服务中对会员店铺及商品等信息进行屏蔽。(二)限制发布商品，指禁止淘宝会员发布新商品。(三)限制发送站内信，指禁止淘宝会员发送站内信。(四)限制社区功能，指禁止淘宝会员使用淘江湖、论坛、帮派、打听等社区类服务。(五)限制买家行为，指禁止淘宝会员购买商品。(六)限制使用阿里旺旺，指禁止淘宝会员使用阿里旺旺。(七)关闭店铺，指删除淘宝会员的店铺，下架店铺内所有出售中的商品，禁止发布商品，并禁止创建店铺。(八)公示警告，指在淘宝会员的店铺页面、商品页面、阿里旺旺界面，对其正在被执行的处理进行公示。(九)查封账户，指永久禁止会员使用违规账户登录淘宝、使用阿里旺旺等工具。</p><h5>第二节 违规处理</h5><h5>第三十九条</h5><p>会员发生违规行为的，其违规行为应当纠正，并扣以一定分值且公布三天。违规扣分在每年的十二月三十一日二十四时清零。因分销商品引起的违规行为，若淘宝判定非分销商责任的，则分销商免于扣分，仅对违规行为进行纠正。</p><h5>第四十条</h5><p>违规行为根据严重程度分为严重违规行为及一般违规行为，两者分别扣分、分别累计、分别执行。严重违规行为，是指严重破坏淘宝经营秩序，并涉嫌违反国家法律法规的行为。一般违规行为，是指除严重违规行为外的违规行为。违规行为根据适用的范围分为通用违规行为及特殊市场违规行为，特殊市场违规行为也遵循前款规定的严重违规行为与一般违规行为划分。</p><h5>第四十一条</h5><p>会员违规行为的纠正，是指：(一)发布违禁信息的，淘宝对会员所发布的违禁商品或信息及因此产生的交易评价进行删除；(二)出售假冒商品的，淘宝对会员所发布的假冒商品或信息进行删除；(三)出售未报关进口商品的，淘宝对会员所发布的未报关进口商品或信息进行删除；(四)盗用他人账户的，淘宝收回被盗账户并使原所有人可以通过账户申诉流程重新取回账户；(五)泄露他人信息的，淘宝对会员所泄露的他人隐私资料的信息进行删除；(六)骗取他人财物的，淘宝对用以骗取他人财物的商品或信息及因此产生的交易评价进行删除；(七)滥发信息的，淘宝删除会员所滥发的商品或信息，或对出售同样商品的两家以上店铺中信用积分较低的副店进行关闭；(八)虚假交易中通过不正当方式提高账户信用积分的，淘宝删除会员虚假交易产生的信用积分以及其两倍数量的其他信用积分，并下架店铺内所有商品；虚假交易中通过不正当方式提高商品销量的，淘宝删除该商品；(九)描述不符的，卖家对商品材质、成份、品质等信息的描述与买家收到的商品严重不符，或导致买家无法正常使用的，淘宝删除该描述不符的商品；卖家未对商品瑕疵等信息进行披露或对商品的描述与买家收到的商品不相符，且影响买家正常使用的，淘宝下架该描述不符的商品。(十)违背承诺的，卖家须偿还延迟发货所规定的违约金，履行如实描述义务或消费者保障服务规定的赔付、退货、换货、维修服务；或卖家须按实际交易价款向买家或淘宝提供发票；(十一)恶意评价的，淘宝或评价方删除该条违规评价；(十二)不当注册的，淘宝查封使用软件、程序方式大批量注册而成的账户；(十三)未依法公开或更新营业执照信息的，卖家公开或更新其营业执照信息；(十四)不当使用他人权利的，淘宝对会员所发布的不当使用他人权利的商品或信息进行删除；(十五)假冒材质成份的，淘宝对会员所发布的假冒材质成份的商品进行删除。</p><h5>第四十二条</h5><p>被执行节点处理的会员，当其全部违规行为被纠正、违规处理期间届满、违规处理措施执行完毕且通过规则考试后，方可恢复正常状态。</p><h5>第四十三条</h5><p>会员可在被违规处理之时起总计十五天内(淘宝审核时间除外)通过线上违规申诉入口提交违规申诉申请。</p><h5>第三节 严重违规行为</h5><h5>第四十四条</h5><p>发布违禁信息，是指会员发布以下国家法律法规禁止发布的商品或信息的行为，包括以下情形：(一)发布以下商品或信息的：1、枪支、弹药、军火或相关器材、配件及仿制品；2、易燃、易爆物品或制作易燃易爆品的相关化学物品；3、毒品、麻醉品、制毒原料、制毒化学品、致瘾性药物、吸食工具及配件；4、含有反动、破坏国家统一、破坏主权及领土完整、破坏社会稳定涉及国家机密、扰乱社会秩序，宣扬邪教迷信，宣扬宗教、种族歧视等内容或相关法律法规禁止出版发行的书籍、音像制品、视频、文件资料；5、人体器官、遗体；6、用于窃取他人隐私或机密的软件及设备；7、正在流通的人民币，伪造变造的货币以及印制设备；8、麻醉注射枪及其相关商品；9、走私、盗窃、抢劫等非法所得；10、可致使他人暂时失去反抗能力、意识模糊的口服或外用的化学品，以及含有黄色淫秽内容的商品、信息；11、涉嫌违反《中华人民共和国文物保护法》相关规定的文物。(二)发布以下商品或信息的：1、管制类刀具及甩棍、弓、弩、飞镖等可能用于危害他人人身安全的管制器具；2、卫星信号的地面收发装置；3、伪造变造的政府机构颁发的文件、证书、公章或仅限国家机关或特定机构方可提供的服务；4、未经许可发布的奥林匹克运动会、世界博览会、亚洲运动会等特许商品；5、赌博用具及作弊工具；6、尚可使用的发票、其它可用于报销的票据以及此类票据的代开服务；7、精神类、麻醉类、有毒类、放射类药品；8、粉末、液态女性催情类商品；9、国家保护动物的活体、肢体、皮毛、标本、器官及制成品，已灭绝动物与现有国家二级以上保护动物的化石；10、身份证及身份证验证设备；11、可能用于侵害他人信息的黑客软件、教程、书籍。(三)发布以下商品或信息的：1、尚可使用的证券、政府发放的消费劵及相应代购、推荐服务；2、军警制服、标志及军警专用制品；3、带有宗教、种族歧视的相关商品或信息；4、有毒化学物、农药及相关信息；5、烟草专卖品；6、含有情色暴力低俗内容的漫画、书籍、游戏、音像制品以及SM用具、成人网站的账号及邀请码、原味二手内衣物、陪聊陪逛服务等情色低俗商品或信息；7、用于预防、治疗人体疾病的药物、血液制品或医疗器械；8、个人隐私信息及企业内部数据；9、国家保护的植物及其制品；10、由不具备生产资质的生产商生产的或不符合国家、地方、行业、企业强制性标准的商品；11、各类短信群发设备及软件；12、撬锁工具、开锁服务及其相关教程、书籍；13、因产品本身质量危及消费者人身、财产安全危险的产品。(四)发布以下商品或信息的：1、可能用于逃避交通管理的商品；2、未经许可的募捐类商品；3、未公开发行的国家级正式考试答案等未被允许公开发行的书籍音像类制品；4、发行时带有银行账户信息的银行卡；5、非法软件或密码破解找回等非法网络服务；6、特供酒、军需酒、自制酒；7、用于全新销售的伪造变造的数码商品；8、经权威质检部门或生产商认定、公布或召回的商品，国家明令淘汰或停止销售的商品，商品本身或外包装上所注明的产品标准、认证标志、成份及含量不符合国家规定的商品，过期、失效、变质的商品，以及含有罂粟籽的食品、调味品、护肤品等制成品；9、非法传销类商品；10、制作毒品、易燃易爆品的方法、书籍；11、利用电话线路上的直流馈电发光的灯；12、国家禁止的集邮票品以及未经邮政行业管理部门批准制作的集邮品，以及一九四九年之后发行的包含“中华民国”字样的邮品；13、算命、超度、风水、做法事等封建迷信类服务；14、一卡多号以及带破解功能的手机卡贴；15、外挂、私服相关的网游类商品。16、官方已停止经营的游戏点卡或平台卡商品；17、虚拟抽奖类商品；18、所有境外（含台服）游戏点卡、货币等相关服务类商品。</p><h5>第四十五条</h5><p>盗用他人账户，是指盗用他人淘宝账户或支付宝账户，涉嫌侵犯他人财产权的行为。</p><h5>第四十六条</h5><p>泄露他人信息，是指未经允许发布、传递他人隐私信息，涉嫌侵犯他人隐私权的行为。</p><h5>第四十七条</h5><p>骗取他人财物，是指以非法获利为目的，非法获取他人财物，涉嫌侵犯他人财产权的行为。</p><h5>第四十八条</h5><p>扰乱市场秩序，是指通过不正当方式，刻意规避淘宝规则或市场管控措施的行为。</p><h5>第四十九条</h5><p>不正当谋利，是指会员采用不正当手段谋取利益的行为，包括：(一)会员向淘宝工作人员及/或其关联人士提供财物、消费、款待或商业机会等；(二)会员系淘宝工作人员及/或其关联人士，且该淘宝工作人员未依据《阿里巴巴集团商业行为准则》规定进行如实申报；(三)会员通过其他手段谋取不正当利益的。</p><h5>第四节 一般违规行为</h5><h5>第五十条</h5><p>滥发信息，是指用户未按本规则要求发布商品或信息，妨害买家权益的行为，包括以下情形：(一)发布以下商品或信息的：1、淘玩偶、淘金币、口碑卡、阿里巴巴及旗下公司提供的各项服务账号、仿阿里巴巴及旗下公司产品的软件；2、未经许可发布专营类目所属商品；3、鱼翅、熊胆及其制品；4、不限时间与流量的、时间不可查询的以及被称为漏洞卡、集团卡、内部卡、测试卡的3G上网资费卡及SIM卡；5、时间不可查询的虚拟服务类商品；6、网络账户死保账号以及腾讯QQ账号；7、平台卡商品其所属平台未经淘宝网备案的；8、自动发货形式的一卡通系列商品；9、未带有平台代充标识的QQ增值业务商品；10、盛付通商品及穿越火线道具类商品；11、秒杀器以及用于提高秒杀成功概率的相关软件设备；12、代写论文、代考试类相关服务；13、法律咨询、心理咨询、金融咨询、医生在线咨询的服务；14、不可查询的分期返还话费类商品；15、虚拟代刷服务类商品；16、慢充卡等实际无法在72小时内到账的虚拟商品；17、群发短信服务、SP业务自消费类商品；18、Itunes账号及用户充值类商品。(二)在商品类、店铺装修区或淘宝门户类页面发布以下信息：1、闲置、收藏商品发布数量超过限制；2、个人手机定位、电话清单查询、银行账户查询；3、代扣驾照分数服务；4、支付宝免签约即时到帐接口；5、尚可使用的外贸单证以及代理报关、清单、商检、单证手续的服务；6、发布心情故事、店铺介绍、外网购物链接、淘宝客链接等非实际交易信息，在tmall.com(天猫)店铺内发布非tmall.com(天猫)店铺购物链接、联系方式、实体店信息、银行账号及个人支付宝账户，在淘宝分销平台外发布批发、代理、招商类商品，在淘宝网商品标题中包含非阿里旺旺联系方式等信息，利用阿里旺旺群发重复的或未经请求的淘宝卖家店铺消息、产品或服务消息等广告消息；7、明示、暗示具有治疗、保健功效或者某种营养素功能的食品，或者以个体经验进行宣传的食品；8、邮局包裹、EMS专递、快递等物流单据凭证及单号；9、大量流通中的外币；10、手机直拨卡与直拨业务，电话回拨卡与回拨业务；11、炒作博客人气、炒作网站人气、代投票类商品或信息；12、捕鸟器及猫狗肉、猫狗皮、猫狗皮制品；13、代他人申请具有专属性的权利，包括申请淘宝店铺装修市场设计师等；14、注射类美白针剂、溶脂针剂、填充针剂、瘦身针剂等用于人体注射的美容针剂类商品。(三)使用以下不当方式发布商品或信息的：1、在禁止发布商品信息的门户、社区、淘江湖、淘宝打听板块发布广告信息的；2、店铺中同时出售同款商品两件以上的；3、开设两家以上店铺且出售同样商品的。(四)在商品类页面、店铺装修区或淘宝门户类页面发布以下错误描述的商品或信息：1、商品信息中缺少标题、主图，或服饰类商品缺少所售商品本身的实物图片；2、商品标题、图片、价格、物流方式、邮费及售后服务等商品要素之间明显不匹配；3、商品标题等信息不实或者与本商品无关的；4、使用虚假的好评率、店铺评分、tmall.com(天猫)店铺类型，或使用与店铺实际信息不符的标识；5、商品与所发布的类目或属性不符的；6、商品与所发布的类目或属性不符且造成严重后果的。</p><h5>第五十一条</h5><p>虚假交易，是指通过不正当方式提高账户信用积分和/或商品销量，妨害买家权益的行为。(一)通过不正当方式所提高的信用积分占账户总信用积分百分之八十以上；(二)通过不正当方式所提高的信用积分占账户总信用积分百分之五十以上但百分之八十以下，且相应违规笔数达四十八笔以上的；(三)其它通过不正当方式提高账户信用积分的；(四)通过不正当方式提高商品销量的。</p><h5>第五十二条</h5><p>延迟发货，是指除定制、预售、适用特定运送方式及tmall.com(天猫)特殊类目的商品外，卖家在买家付款后明确表示缺货或实际未在七十二小时内发货，妨害买家权益的行为。买卖双方另有约定的除外。卖家的发货时间，以快递公司系统内记录的时间为准。</p><h5>第五十三条</h5><p>描述不符，是指买家收到的商品与达成交易时卖家对商品的描述不相符，卖家未对商品瑕疵、保质期、附带品等必须说明的信息进行披露，妨害买家权益的行为，包括以下情形：(一)卖家对商品材质、成份、品质等信息的描述与买家收到的商品严重不符，或导致买家无法正常使用的；(二)卖家未对商品瑕疵等信息进行披露或对商品的描述与买家收到的商品不相符，且影响买家正常使用的；(三)卖家未对商品瑕疵等信息进行披露或对商品的描述与买家收到的商品不相符，但未对买家正常使用造成实质性影响的。</p><h5>第五十四条</h5><p>违背承诺，是指卖家未按照承诺向买家提供以下服务，妨害买家权益的行为，包括以下情形：(一)卖家违背以下承诺的：1、淘宝判定卖家确实应该承担因消费者保障服务产生的退货退款等售后保障责任但卖家拒绝承担的；2、淘宝判定卖家确实应该承担七天无理由退换货、假货赔三、数码维修、闪电发货赔付等售后保障责任但卖家拒绝承担的；3、卖家在收到消费者保障服务保证金催缴单之日起十四日内未足额缴纳保证金的；4、卖家参与试用中心的活动，但却在买家报名完成后拒绝向买家发送已承诺提供的试用商品的。(二)卖家违背以下承诺的：1、tmall.com(天猫)卖家拒绝提供或者拒绝按照承诺的方式提供发票的；2、买家选择支付宝担保交易，但卖家拒绝使用；tmall.com(天猫)卖家与买家在tmall.com(天猫)外进行交易的；3、加入货到付款或信用卡付款服务的卖家，但拒绝提供上述服务的；4、加入淘宝官方活动的卖家，未按照活动要求提供服务的；5、发布拍卖商品的卖家，拒绝按照买家拍下的价格成交或者拒绝提供包邮服务的；6、加入聚划算的卖家中途退出，或未在七天内按已审核的报名信息所载内容完成发货的；7、加入淘宝游戏交易平台的卖家，未在买家付款后三十分钟内提供商品的；8、加入闪电发货的卖家，出售虚拟商品的未在一小时内完成发货，或出售实物商品的未在二十四小时内发货的；(三)卖家未履行其它承诺的。</p><h5>第五十五条</h5><p>竞拍不买，是指买家拍得商品后拒不按其最终出价购买商品，妨害卖家权益的行为。</p><h5>第五十六条</h5><p>恶意骚扰，是指卖家采取恶劣手段骚扰买家，妨害买家权益的行为。</p><h5>第五十七条</h5><p>不当注册，是指用户通过软件、程序等方式，大批量注册淘宝账户，妨害淘宝运营秩序的行为。</p><h5>第五十八条</h5><p>未依法公开或更新营业执照信息，指通过支付宝实名认证中企业类认证的卖家，未在淘宝规定的期间内公开或及时更新其营业执照信息的行为。</p><h5>第五十九条</h5><p>不当使用他人权利，是指用户发生以下行为：(一)卖家在所发布的商品信息或所使用的店铺名、域名等中不当使用他人商标权、著作权等权利的；(二)卖家出售商品涉嫌不当使用他人商标权、著作权、专利权等权利的；(三)卖家所发布的商品信息或所使用的其他信息造成消费者混淆、误认或造成不正当竞争的。同一权利人在三天内对同一卖家的投诉视为一次投诉。</p><h5>第五节 违规的执行</h5><h5>第六十条</h5><p>用户的违规行为，通过淘宝会员、权利人的投诉或淘宝排查发现。</p><h5>第六十一条</h5><p>对违规行为的投诉，除滥发信息、虚假交易、不当注册、发布违禁信息、出售假冒商品、不当使用他人权利、盗用他人账户、泄露他人信息可随时提交投诉外，其余须在以下规定时间内进行投诉；未在规定时间内投诉的，不予受理：（一）延迟发货的投诉时间，为交易关闭后十五天内；（二）违背承诺的投诉时间，为买家拍下后十五天内；（三）描述不符、骗取他人财物的投诉时间为交易成功后十五天内；（四）恶意评价的投诉时间为评价生效后十五天内。</p><h5>第六十二条</h5><p>对延迟发货、违背承诺及恶意评价的违规行为，被投诉人须在被投诉之日起三天内提交证据。逾期未提交证据的，淘宝有权根据当时所掌握的情况进行判断与处理。对其余违规行为的判断与处理，淘宝在收到投诉后立即进行。</p><h5>第六十三条</h5><p>卖家自行作出的承诺或说明与本规则相悖的，淘宝不予采信。除证据有误或判断错误的情形外，对违规行为的处理不中止、不撤销。</p><h5>分则</h5><h5>第一篇 淘宝网</h5><h5>第一章 交易</h5><h5>第一节 注册</h5><h5>第一条</h5><p>淘宝网会员的店铺名中不得包含旗舰、专卖等词语。</p><h5>第二节 经营</h5><h5>第二条</h5><p>已创建的店铺若连续六周出售中的商品数量均为零，淘宝有权将该店铺释放。一个淘宝网会员仅能拥有一个可出售商品的账户。</p><h5>第三条</h5><p>淘宝网会员账户已绑定通过实名认证的支付宝账户，即可发布闲置商品，但创建店铺后方可发布全新及二手商品。淘宝网会员发布商品的数量可能受到以下限制：(一)淘宝网有权根据卖家所经营的类目、信用积分调整其商品发布数量上限，但被调整后的可发布商品数量不少于一百件；(二)淘宝网卖家发布闲置书籍、闲置音像类商品不得超过五十件，且同款闲置书籍不得超过一件。</p><h5>第二章 淘宝网市场管理</h5><h5>第一节 市场管理情形</h5><h5>第四条</h5><p>卖家应妥善管理其所发布的商品信息。对近九十天内未编辑、未经浏览且未成交的商品，淘宝网将定期进行商品下架。</p><h5>第三章 违规</h5><h5>第一节 淘宝网严重违规行为及扣分分值</h5><h5>第五条</h5><p>发布违禁信息：(一)发布总则中发布违禁信息第一项所列商品或信息的，每次扣四十八分；(二)发布总则中发布违禁信息第二项所列商品或信息的，每次扣十二分；(三)发布总则中发布违禁信息第三项所列商品或信息的，每次扣六分；(四)发布总则中发布违禁信息第四项所列商品或信息的，除第八目外每次扣两分；发布第八目商品或信息的，加入“第三方质检服务”的淘宝网卖家每次扣六分，其他淘宝网卖家每次扣两分。对于系统排查到的涉嫌违禁的商品，淘宝将在两个工作日内进行人工排查，人工排查期间给予单个商品监管；对于系统排查到的涉嫌重复发布违禁信息的卖家所发布的商品，淘宝将在两个工作日内进行人工排查，人工排查期间给予单个商品搜索屏蔽。</p><h5>第六条</h5><p>盗用他人账户，每次扣四十八分。</p><h5>第七条</h5><p>泄露他人信息，每次扣六分。</p><h5>第八条</h5><p>骗取他人财物，每次扣四十八分。对于系统排查到的涉嫌骗取他人财物的商品信息，淘宝将在两个工作日内进行人工排查，人工排查期内给予单个商品搜索屏蔽或降权。对于系统排查到的涉嫌骗取他人财物的会员，淘宝将视情节采取警告、强制身份验证等安全保护措施。会员为卖家的，将下架店铺内所有商品，且将未完结交易的确认收货时间延长三十天；或直接进行店铺监管。</p><h5>第九条</h5><p>扰乱市场秩序，每次扣二十四分；情节严重的，每次扣四十八分。对涉嫌扰乱市场秩序的商品，淘宝将给予单个商品淘宝网搜索降权直至商品整改完成后第三天恢复。同时根据店铺内此类商品数量给予卖家七至九十天的全店商品淘宝网搜索降权。</p><h5>第十条</h5><p>出售假冒商品：(一)卖家出售假冒、盗版商品且情节特别严重的，每次扣四十八分；(二)卖家出售假冒、盗版商品且情节严重的，每次扣二十四分；(三)卖家出售假冒、盗版商品的，每次扣十二分；(四)卖家涉嫌出售假冒、盗版商品的，每件扣两分（三日内不超过十二分）；情节严重的，每次扣二十四分。为了防止对公众造成不利影响，保护消费者权益，对涉嫌违反上述情形的卖家，淘宝将视情节严重程度给予店铺监管。</p><h5>第十一条</h5><p>不正当谋利，无论是否获得利益，每次扣二十四分；情节严重的，每次扣四十八分。</p><h5>第二节 淘宝网一般违规行为及处理</h5><h5>第十二条</h5><p>滥发信息：(一)发布总则中滥发信息第一项所列商品或信息的，每次扣十二分；对系统排查到的涉嫌违反上述规定的商品，淘宝网将在两个工作日内进行人工排查，人工排查期内给予单个商品监管。(二)在淘宝网商品类页面中发布总则中滥发信息第二项所列商品或信息的，每件扣零点二分（但三天内累计扣分不超过七分）；若在店铺装修区、阿里旺旺或淘宝门户类页面发布的，每次扣四分；(三)发布总则中滥发信息第三项所列商品或信息的，除第三目每次扣六分之外，每件商品或信息扣零点二分（但三天内累计扣分不超过七分）；对系统排查到的涉嫌违反第二目规定的商品，淘宝网将在两个工作日内进行人工排查，人工排查期内给予单个商品淘宝网搜索屏蔽。(四)在淘宝网商品类页面中发布总则中滥发信息第四项所列商品或信息的，除第六目每次扣十二分外，每件扣零点二分（但三天内累计扣分不超过七分）；若在店铺装修区或淘宝门户类页面发布的，每次扣四分；对违反第四项中第二、三、五、六目规定的商品，淘宝网同时给予单个商品淘宝网搜索降权直至商品整改完成后第三天恢复。卖家若发布除以上情形之外其它有违公序良俗的商品或信息，淘宝网可对商品或信息进行临时性下架或删除。</p><h5>第十三条</h5><p>虚假交易：(一)符合总则中虚假交易第一项所述情形的，若相应违规笔数达九十六笔以上的，每次扣九十六分；若相应违规笔数四十八笔以上但九十六笔以下的，每次扣四十八分；(二)符合总则中虚假交易第二项所述情形的，每次扣四十八分；(三)符合总则中虚假交易第三项所述情形的，每次扣二十四分；(四)符合总则中虚假交易第四项所述情形的，每次扣六分。淘宝网对涉嫌虚假销量、信用的商品给予三十天的单个商品淘宝网搜索降权，同时根据卖家店铺涉嫌虚假交易情节严重程度给予卖家七至九十天的全店商品淘宝网搜索降权。</p><h5>第十四条</h5><p>延迟发货，每次扣三分，同时需向买家支付该商品实际成交金额的百分之五作为违约金，且金额最高不超过三十元。</p><h5>第十五条</h5><p>描述不符：(一)符合总则中描述不符第一项所述情形的，每次扣十二分；(二)符合总则中描述不符第二项所述情形的，每次扣六分；(三)符合总则中描述不符第三项所述情形的，每次扣三分。</p><h5>第十六条</h5><p>违背承诺：(一)符合总则中违背承诺第一项所述情形的，每次扣十二分；(二)符合总则中违背承诺第二项所述情形的，每次扣六分；(三)符合总则中违背承诺第三项所述情形的，每次扣四分。</p><h5>第十七条</h5><p>竞拍不买，每次扣十二分，并须将拍卖流程中最终冻结的拍卖保证金赔偿给卖家。</p><h5>第十八条</h5><p>恶意骚扰，每次扣十二分。</p><h5>第十九条</h5><p>不当注册，每次扣十二分。</p><h5>第二十条</h5><p>未依法公开或更新营业执照信息，或未在其营业执照信息变更完成之日起三十天内未更新，每次扣十二分。</p><h5>第二十一条</h5><p>不当使用他人权利，每次扣两分。</p><h5>第二十二条</h5><p>恶意评价，是指买家、同行竞争者等评价人以给予中、差评的方式谋取额外财物或其它不当利益的行为。评价人恶意评价，每次扣十二分。</p><h5>第三节 违规处理</h5><h5>第二十三条</h5><p>淘宝网对会员的严重违规行为采取以下违规处理方式：(一)会员严重违规扣分累计达十二分的，给予店铺屏蔽、限制发布商品、限制创建店铺、限制发送站内信、限制社区功能及公示警告七天的处理；(二)会员严重违规扣分累计达二十四分的，给予店铺屏蔽、下架店铺内所有商品、限制发布商品、限制创建店铺、限制发送站内信、限制社区功能及公示警告十四天的处理；(三)会员严重违规扣分累计达三十六分的，给予关闭店铺、限制发送站内信、限制社区功能及公示警告二十一天的处理；(四)会员严重违规扣分累计达四十八分的，给予查封账户的处理。会员因单次违规扣分较大，导致累积扣分满足多个节点处理条件的，或在违规处理期间又须执行同类节点处理的，仅执行最重的节点处理。</p><h5>第二十四条</h5><p>淘宝网对会员的一般违规行为采取以下违规处理方式：会员因一般违规行为，每扣十二分即被给予店铺屏蔽、限制发布商品及公示警告十二天的节点处理。若会员因恶意评价或竞拍不买被扣分，则在随后的节点处理中将对其增加限制买家行为的处理。</p><h5>第四章 特殊市场</h5><h5>第一节 专营类目</h5><h5>第二十五条</h5><p>成人避孕用品计生用品类目，是供符合特定条件的淘宝网卖家发布该类目所属商品的网络交易平台。卖家加入该类目后，店铺内仅允许出售成人用品，且露点、纱状、网状、镂空的情趣内衣、成人用具商品中的敏感部位均须采用淘宝指定的遮盖物进行修饰。卖家若同时满足以下条件，即可加入成人避孕用品计生用品类目：(一)提供真实、有效的身份信息进行备案；(二)未在近三个月退出该类目。</p><h5>第二十六条</h5><p>书籍杂志报纸类目，是供符合特定条件的淘宝网卖家发布该类目所属商品的网络交易平台。卖家若同时满足以下条件，即可加入书籍杂志报纸类目：(一)普通卖家需具备《出版物经营许可证》，代购卖家需提供境内网站代购账户等级信息及近一个月的代购订单信息；(二)未曾两次退出该类目。卖家若发生以下任一情形，即被书籍杂志报纸类目清退：(一)一般违规行为扣分累计达十二分；(二)严重违规行为扣分累计达十二分。</p><h5>第二十七条</h5><p>音乐影视明星音像类目，是供符合特定条件的淘宝网卖家发布该类目所属商品的网络交易平台。卖家若同时满足以下条件，即可加入音乐影视明星音像类目：(一)普通卖家需具备《音像制品经营许可证》，代购卖家需提供境内网站代购账户等级信息及近一个月的代购订单信息；(二)未曾两次退出该类目。卖家若发生以下任一情形，即被音乐影视明星音像类目清退：(一)一般违规行为扣分累计达十二分；(二)严重违规行为扣分累计达十二分。</p><h5>第二十八条</h5><p>保险类目，是供符合特定条件的淘宝网卖家发布该类目所属商品的网络交易平台。卖家若同时满足以下条件，即可加入保险类目：(一)卖家为经营保险服务的国家级总公司；(二)符合类目运营要求。</p><h5>第二十九条</h5><p>彩票类目，是供符合特定条件的淘宝网卖家发布该类目所属商品的网络交易平台。卖家若同时满足以下条件，即可加入彩票类目：(一)卖家为省级以上彩票管理发行销售机构；(二)符合类目运营要求。</p><h5>第三十条</h5><p>淘宝旅行类目，是供符合特定条件的淘宝网卖家发布该类目所属商品的网络交易平台。卖家若同时满足以下条件，即可加入淘宝旅行类目：(一)出售机票的卖家须具有中华人民共和国《企业法人营业执照》、中国民用航空局及各分局授予的销售终端ID号；(二)出售机票的卖家须提供《中国民用航空运输销售代理业务资格许可证书》或《国际航协机票代理资格证书》，其中出售国际机票的卖家须提供一类资质证书；(三)出售机票的卖家账户须通过支付宝商家认证；(四)出售国际机票的卖家在支付宝账户中存入五万元保证金，出售国内机票卖家在支付宝账户中存入一万元保证金；(五)出售机票的卖家未在近三个月退出机票类目；(六)出售旅行线路的卖家须具有中华人民共和国《企业法人营业执照》及《旅行社经营许可证》；(七)符合类目运营要求。卖家发生以下任一情形，即被淘宝旅行类目清退：(一)出售国内机票的卖家一个自然月内，未在五分钟内响应或未在十五分钟内处理完毕买家的退改签申请达两次以上，或接受买家的退款申请后未在十五分钟内处理完毕达两次以上，或成功受理订单后以任何理由拒绝出票达两次以上，或未及时为买家提供行程单或发票达两次以上的；(二)出售国际机票的卖家连续三次出现当月卖家账户中买家通过支付宝付款订单占所有买家拍下订单的比例达百分之六十以下，或当月成功出票订单占买家通过支付宝付款订单的比例达百分之八十以下，或当月未在买家付款成功后十五分钟内受理订单达五次以上，或当月未在受理订单后四十五分钟内处理成功达五次以上，或当月未帮助买家订座达五次以上，或当月未及时按买家要求提供退改签服务达五次以上，或当月未在订单成功后十五天内向买家邮寄行程单、发票达五次以上，或当月未及时答复买家咨询导致买家投诉达五次以上的；(三)出售国内机票的卖家擅自发布国内机票以外商品，出售国际机票的卖家擅自发布《国际电子客票平台合作协议》中未规定商品的；(四)出售机票的卖家不再符合前款之类目加入条件的；(五)出售机票的卖家交易成功后，因卖家过失导致买家未能顺利登机的；(六)出售机票的卖家向用户收取除商品价金及税费以外的费用的；(七)出售旅行线路的卖家因骗取他人财物而被扣分的。(八)出售机票、旅行线路的卖家与买家在其他网站或线下进行交易的。</p><h5>第三十一条</h5><p>酒类制品类目，是供符合特定条件的淘宝网卖家发布该类目所属商品的网络交易平台。卖家若同时满足以下条件，即可加入酒类制品类目：(一)卖家需具备有效的《酒类流通备案登记表》；(二)未在近三个月退出该类目；(三)符合类目运营要求。卖家若发生以下任一情形，即被酒类制品类目清退：(一)卖家出售假冒商品；(二)卖家未在其《酒类流通备案登记表》到期后的三十天内更新《酒类流通备案登记表》信息。</p><h5>第三十二条</h5><p>淘宝网虚拟物品类目，是供符合特定条件的淘宝网卖家发布网络游戏点卡、腾讯QQ专区、移动/联通/电信充值中心、网游装备/游戏币/帐号/代练、淘宝网络游戏虚拟物品交易平台、手机号码/套餐/增值业务类目所属商品的网络交易平台。卖家若同时满足以下条件，即可加入淘宝网虚拟物品类目：(一)主营类目为网络游戏点卡、腾讯QQ专区、移动/联通/电信充值中心的卖家须加入消费者保障服务并足额缴纳保证金；(二)淘宝游戏虚拟物品交易平台卖家需签订并遵守淘宝网《网络游戏虚拟物品交易服务协议》
 ；(三)网络游戏点卡平台商及该平台商品卖家、3G上网卡卖家需提供真实、有效的身份信息进行备案 
；(四)充值软件市场卖家需遵守《充值软件市场管理规则》；(五)卖家所售商品须仅支持支付宝交易；(六)符合类目运营要求；(七)未在近两个月退出该类目的。卖家若发生以下任一情形，即被虚拟物品类目清退：(一)主营类目为网络游戏点卡、腾讯QQ专区、移动/联通/电信充值中心的卖家退出消费者保障服务的；(二)卖家的退款率、纠纷退款率等指标不符合类目指定标准的；(三)卖家所售商品拒绝支持支付宝交易的；(四)卖家在买家付款前且商品信息显示有足够库存的情况下，以任何理由表示缺货的；(五)卖家所售商品到账时间超过七十二小时的；(六)严重违规行为累计扣分达十二分以上的。</p><h5>第二节 良无限</h5><h5>第三十三条</h5><p>良无限，是指由特定企业卖家及个体工商户卖家销售非知名品牌或无品牌优质商品的网络交易平台，域名为lp.taobao.com。</p><h5>第三十四条</h5><p>卖家必须满足以下条件，才有权申请加入良无限：（一）卖家持有完成有效年检的《企业法人营业执照》或《个体工商户营业执照》且拟在良无限开展的经营活动不超过其《企业法人营业执照》或《个体工商户营业执照》核准的经营范围；（二）卖家及其销售的商品符合《良无限申请资质及条件》；（三）卖家有效签署《良无限服务协议》及其相关附属协议；（四）卖家未在近六个月退</p><h5>第三十五条</h5><p>卖家若发生以下任一情形，即被良无限清退：（一）退出消费者保障服务的；（二）退出七天无理由退换货服务的；（三）退出闪电发货服务的；（四）提供伪造、变造的卖家资质或商品资料；（五）发布《良无限限制品牌参考目录》所属品牌商品三次以上的;（六）卖家不再符合《良无限质量认证体系》要求的；（七）已通过良无限质量认证的卖家，实际所售商品与认证时提供的商品不一致两次以上的。</p><h5>第三节 淘宝开放平台</h5><h5>第三十六条</h5><p>淘宝开放平台，是指淘宝网上为从事软件开发的第三方软件开发者提供应用发布和接入的平台，域名为open.taobao.com。</p><h5>第三十七条</h5><p>淘宝开放平台的管理应遵循《淘宝开放平台管理规范》的规定。</p><h5>第二篇 tmall.com (天猫)</h5><h5>第一章 交易</h5><h5>第一节 注册</h5><h5>第一条</h5><p>商家必须满足以下条件，才有权申请加入tmall.com(天猫)：(一)商家及其销售的商品符合当年度的《tmall.com(天猫)招商标准》；(二)商家及其销售的商品符合当年度的《tmall.com(天猫)行业标准》；(三)商家有效签署《tmall.com(天猫)服务协议》及其相关附属协议；(四)商家符合tmall.com(天猫)要求的其他条件。</p><h5>第二条</h5><p>商家若发生以下任一情形，tmall.com(天猫)有权清退：(一)未经商标注册人同意，更换其注册商标并将该更换商标的商品进行销售的；(二)符合本规则总则中不当使用他人权利且情节严重的；(三)向tmall.com(天猫)提供伪造、变造的商家资质或商品资料；(四)未达到tmall.com(天猫)的试运营考核或季度考核标准的；(五)商家违反《tmall.com(天猫)招商标准》的；(六)严重违规扣分达四十八分。</p><h5>第二节 经营</h5><h5>第三条</h5><p>商家会员的会员名、店铺名的命名应当严格遵守《tmall.com(天猫)店铺命名规范》。</p><h5>第四条</h5><p>商家在tmall.com(天猫)发布商品应当严格遵守《tmall.com(天猫)行业标准》。</p><h5>第二章 tmall.com(天猫)市场管理</h5><h5>第一节 市场管理情形</h5><h5>第五条</h5><p>tmall.com(天猫)商家及商品参与淘宝网、一淘网等其他市场的活动及展现时，应同时遵循其他市场的市场管理规定。</p><h5>第六条</h5><p>商家应根据协议约定或规则规定缴纳或补足保证金。若保证金余额不足，且经淘宝催缴后未在十五天内缴纳或不足的，给予店铺监管，直至足额缴纳或为止。</p><h5>第三章 违规</h5><h5>第一节 tmall.com(天猫)对严重违规行为及处理</h5><h5>第七条</h5><p>发布违禁信息：(一)发布总则中发布违禁信息第一项所列商品或信息的，每次扣四十八分；(二)发布总则中发布违禁信息第二项所列商品或信息的，每次扣十二分；(三)发布总则中发布违禁信息第三项所列商品或信息的，每次扣六分；(四)发布总则中发布违禁信息第四项所列商品或信息的，除第八目每次扣六分外，其他商品或信息每次扣两分。对于系统排查到的涉嫌违禁的商品，tmall.com(天猫)会在两个工作日内进行人工排查，人工排查期间给予单个商品监管；对于系统排查到的涉嫌重复发布违禁信息的卖家所发布的商品，tmall.com(天猫)会在两个工作日内进行人工排查，人工排查期间给予单个商品搜索屏蔽。</p><h5>第八条</h5><p>盗用他人账户，每次扣四十八分。</p><h5>第九条</h5><p>泄露他人信息，每次扣六分。</p><h5>第十条</h5><p>骗取他人财物，每次扣四十八分。对于系统排查到的涉嫌骗取他人财物的商品信息，tmall.com(天猫)将在两个工作日内进行人工排查，人工排查期内给予单个商品搜索屏蔽或降权。对于系统排查到的涉嫌骗取他人财物的会员，tmall.com(天猫)将视情节采取警告、强制身份验证等安全保护措施，会员为卖家的，将下架店铺内所有商品，且将未完结交易的确认收货时间延长三十天；或直接进行店铺监管。</p><h5>第十一条</h5><p>扰乱市场秩序，每次扣二十四分；情节严重的，每次扣四十八分。对涉嫌扰乱市场秩序的商品，tmall.com(天猫)给予单个商品淘宝网搜索降权直至商品整改完成后第三天恢复。同时根据店铺内此类商品数量给予卖家七至九十天的全店商品淘宝网搜索降权。</p><h5>第十二条</h5><p>出售假冒商品，每次扣四十八分。为了防止对公众造成不利影响，保护消费者权益，对涉嫌违反上述情形的卖家，tmall.com(天猫)视情节严重程度给予店铺监管。</p><h5>第十三条</h5><p>假冒材质成份，是指商家对商品全部材质或成份信息的描述与买家收到的商品完全不符的。假冒材质成份的不符，每次扣四十八分。</p><h5>第十四条</h5><p>出售未经报关进口商品，是指出售未经正常中国海关报关程序的进口商品。出售未经报关进口商品的，每次扣四十八分。</p><h5>第十五条</h5><p>发布非约定商品，是指商家发布未经tmall.com(天猫)许可的品牌的商品。每次扣十二分。</p><h5>第十六条</h5><p>不正当谋利，无论是否获得利益，每次扣二十四分；情节严重的，每次扣四十八分。</p><h5>第二节 tmall.com(天猫)对一般违规行为及处理</h5><h5>第十七条</h5><p>滥发信息：(一)发布总则中滥发信息第一项所列商品或信息的，每次扣十二分；(二)在tmall.com(天猫)商品类页面中发布总则中滥发信息第二项所列商品或信息的，每件扣一分（但三天内累计扣分不超过七分）；若在店铺装修区、阿里旺旺或淘宝门户类页面发布的，每次扣四分；(三)发布总则中滥发信息第三项所列商品或信息的，除第三目每次扣六分之外，每件商品或信息扣一分（但三天内累计扣分不超过七分）；对系统排查到的涉嫌违反第二目规定的商品，tmall.com(天猫)将在两个工作日内进行人工排查，人工排查期内给予单个商品淘宝网搜索屏蔽。(四)在tmall.com(天猫)商品类页面中发布总则中滥发信息第四项所列商品或信息的，除第六目每次扣十二分外，每件扣一分（但三天内累计扣分不超过七分）；若在店铺装修区或淘宝门户类页面发布的，每次扣四分；对违反第四项中第二、三、五、六目规定的商品，tmall.com(天猫)同时给予单个商品淘宝网搜索降权直至商品整改完成后第三天恢复。(五)除定制、预售、适用特定运送方式及tmall.com(天猫)特殊类目的商品外，商家在买家付款前且商品信息显示有足够库存的情况下，以任何理由表示不能在七十二小时内完成发货的，每次扣两分。商家若发布除以上情形之外其它有违公序良俗的商品或信息，淘宝可对商品或信息进行临时性下架或删除。</p><h5>第十八条</h5><p>虚假交易：(一)符合总则中虚假交易第四项所述情形的，每次扣六分。淘宝对涉嫌虚假销量、信用的商品给予三十天的单个商品淘宝网搜索降权，同时根据卖家店铺涉嫌虚假交易情节严重程度给予卖家七至九十天的全店商品淘宝网搜索降权。</p><h5>第十九条</h5><p>延迟发货，商家需向买家支付该商品实际成交金额的百分之三十作为违约金，且金额最高不超过五百元。</p><h5>第二十条</h5><p>描述不符：(一)符合总则中描述不符第一项所述情形的，每次扣十二分；(二)符合总则中描述不符第二项所述情形的，每次扣六分；(三)符合总则中描述不符第三项所述情形的，每次扣三分。</p><h5>第二十一条</h5><p>违背承诺：(一)符合总则中违背承诺第一项所述情形的，每次扣十二分；(二)符合总则中违背承诺第二项所述情形的，每次扣六分；(三)符合总则中违背承诺第三项所述情形的，每次扣四分。</p><h5>第二十二条</h5><p>买家竞拍不买，每次扣十二分，并须将拍卖流程中最终冻结的拍卖保证金赔偿给商家。</p><h5>第二十三条</h5><p>恶意骚扰，每次扣十二分。</p><h5>第二十四条</h5><p>不当注册，每次扣十二分。</p><h5>第二十五条</h5><p>未依法公开或更新营业执照信息的，或未在其营业执照信息变更完成之日起三十天内未更新的，每次扣十二分。</p><h5>第二十六条</h5><p>不当使用他人权利，每次扣两分。</p><h5>第三节 违规处理</h5><h5>第二十七条</h5><p>tmall.com(天猫)对会员的严重违规行为采取以下违规处理方式：(一)商家严重违规扣分累计达十二分的，给予店铺屏蔽、限制发布商品、限制创建店铺、限制发送站内信、限制社区功能及公示警告七天、限制参加tmall.com(天猫)营销活动三十天、向tmall.com(天猫)支付违约金两万元的处理；(二)商家严重违规扣分累计达二十四分的，给予店铺屏蔽、下架店铺内所有商品、限制发布商品、限制创建店铺、限制发送站内信、限制社区功能及公示警告十四天、限制参加tmall.com(天猫)营销活动六十天、向tmall.com(天猫)支付违约金三万元的处理；(三)商家严重违规扣分累计达三十六分的，给予关闭店铺、限制发送站内信、限制社区功能及公示警告二十一天、限制参加tmall.com(天猫)营销活动九十天、向tmall.com(天猫)支付违约金四万元的处理；(四)商家严重违规扣分累计达四十八分的，对商家做清退处理，查封账户并扣除全部保证金作为违约金（该保证金是指商家入驻tmall.com(天猫)时缴纳的保证金额度）。商家因单次违规扣分较大，导致累积扣分满足多个节点处理条件的，或在被处理期间又须执行同类节点处理的，仅执行最重的节点处理。</p><h5>第二十八条</h5><p>tmall.com(天猫)对会员的一般违规行为采取以下违规处理方式：商家因一般违规行为，每扣十二分即被给予限制参加tmall.com(天猫)营销活动七天、向tmall.com(天猫)支付违约金一万元的处理。</p><h5>附则</h5><h5>第一条</h5><p>淘宝用户的行为，发生在本规则生效或修订以前的，适用当时的规则；发生在本规则生效或修订以后的，适用本规则。</p><h5>第二条</h5><p>本规则中的以上，包含本数；本规则中的以下，不包含本数。</p><h5>附件</h5><p>一、查看</p><ul class=" list-paddingleft-2"><li><p><a target="_blank" href="https://a.taobao.com/detail/2011/12/30/569607/1.php?spm=1.176007.209262.6">旧版《淘宝规则》</a></p></li><li><p>（2011年12月31日前违规适用）</p></li></ul><p>​<br></p>
